<template>
	<div id="vip-redpacket-record">
		<public-header title="购买记录" v-if="!isWeixin"></public-header>
		<div class="content">
			<van-list
				v-model="records.loading"
				:finished="records.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="false"
				v-if="!records.empty"
			>
				<div class="record-item" v-for="(item, index) in records.data" :key="index">
					<div class="inner flex-lr van-hairline--bottom">
						<div>
							<div>{{item.title}}</div>
							<div class="font-12 c-gray margin-5-t">购买日期：{{item.addtime_cn}}</div>
						</div>
						<div class="c-danger">
							{{Lang.dollarSign}}<span class="font-20">{{item.final_fee}}</span>
						</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="loaded" v-if="records.empty">
				<div class="loaded-tips">暂无购买记录</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records: {
					page: 1,
					psize: 15,
					loading: true,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true
			}
		},
		components: {
			PublicHeader
		},
		methods: {
			onLoad() {
				var that = this;
				getRecords({
					vue: that,
					url: 'vipRedpacket/index/record',
					data: {
						page: that.records.page,
						psize: that.records.psize
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#vip-redpacket-record #public-header .van-nav-bar{
		background-color: #353130!important;
	}
	#vip-redpacket-record .record-item{
		padding-left: 15px;
		background-color: #fff;
		font-size: 14px;
	}
	#vip-redpacket-record .record-item .inner{
		width: 100%;
		padding: 15px 15px 15px 0;
	}
</style>


